*{
    color:black;
}

.headercontainer{
    
    display: flex;
    flex-direction: column;
     position: sticky;
        top:0;
        background-color: white;
}

.headertitle{
    flex: 1;
}

.body{
    
}

.anchorarticle{
    text-decoration: none;
    margin-top: 10px;
}

ul{
    padding-left: 10px;
}
.anchorarticle:hover{
    background-color: darksalmon;
}
.articlecontent{
    padding-right: 30px;
    padding-left: 30px;
    margin-top: 25px;
}

.articletitle{
    font-size: x-large;
    font-weight: bold;
    font-family: initial;

}

.articletext{
    display: flex;
    flex-direction: row;
    justify-content: space-evenly;
    padding-top: 30px;

}

.abstract{
padding: 20px 20px;
font-family: initial;
}

.publish {
   margin: 10px 10px;
   margin-bottom: 30px; 
}
.headerul{
    display: flex;
    justify-content: center;
    list-style: none;
    border-bottom: solid black;
    position: sticky;
}

.Reacttimes{
    font-family: 'Grenze Gotisch', cursive;
    font-size: xxx-large;
    margin: 0;
}

.headerbutton{
    text-decoration: none;
    cursor: pointer;
    border-radius: 20px;
    width: 80px;
}

.headerbutton:hover{
    background-color: black;
    color:white;
    text-decoration: underline;
}

.articlecontent{
    display: flex;
    flex-direction: column;
    margin-left:200px;
    margin-right:200px;
    margin-bottom: 30px;
}

.articlecontent:hover{
    background-color: gray;
}

.footer{
    background-color: blueviolet;
}

.headerul li{
    padding: 5px 5px;
    font-size: medium;
    font-weight: bold;
}

@media screen and (max-width: 600px) {
    .headerul{
        justify-content: start;
        
    }

    .headercontainer{
        overflow:auto;
        position: sticky;
        top:0;

    }

    .articlecontent{
    display: flex;
    flex-direction: column;
    padding-left: 2px;
    padding-right: 2px;
    margin-left: 0;
    margin-right: 0;
  }

  .articletext{
      display: flex;
      flex-direction: column;
      align-items: center;
  }

  .articletitle{
    font-size: large;
    font-weight: bold;
}

}